* {
  box-sizing: border-box;
}
body {
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgb(4, 0, 51);
  
}
.btn {
  background-color: #ffffff;
  color: #03f0d0;
  font-family: 'Montserrat', sans-serif;
  text-transform: uppercase;
  margin: 15px;
  padding: 5px;
  border: 2px solid #8b3e8b;
  border-radius: 5px;
  box-shadow: 0 12px #03f0d0, 0 18px rgba(0, 0, 0, 0.4);
  font-size: 60px;
  width: 90px;
  height: 80px;
  cursor: pointer;
  position: relative;
  top: 0;
}

.btn:hover {
  background-color: #b4b8da;
  color: #0e80b0;
  top: 4px;
  box-shadow: 0 8px #03f0d0, 0 14px rgba(0, 0, 0, 0.4);
}
.btn:active {
    top: 8px;
    box-shadow: 0 4px #c2bb11, 0 10px rgba(0, 0, 0, 0.4);
  }
.game {
  display: flex;
  flex-wrap: wrap;
}
.game-info {
  margin: 15px;
  color: #ffffff;
}

.board > div {
  display: flex;
}